.pokemons-container-buttons {
    display: flex;
    justify-content: center;
    margin-top: 1rem; /* Adjust margin as needed */
  }
  
  .pokemons-container-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 6px;
    background-color: transparent;
    color: #a18aba;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    margin: 0 0.5rem; /* Adjust margin as needed */
  }
  
  .pokemons-container-button:hover {
    background-color: #1b082f;
    color: #ebe7ef;
  }
  
  .pokemons-container-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .pokemons-container-button.active {
    background-color: #7925d3;
    color: #ebe7ef;
  }
  